/*Βαζω στυλ με css για τα κουμπία,του πίνακες,και όλα τα γραφικά συστατικά τις σελίδας */

/*ΣΤΥΛ ΓΙΑ ΤΟ CONTAINER ΤΗΣ ΣΕΛΙΔΑΣ ΠΟΥ ΒΑΖΩ ΟΛΑ ΤΑ ΣΤΟΙΧΕΙΑ*/
.dashboard-container {
  background-color: #b4d4cc;
  width: 100%;
  height: 100vh;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  overflow-y: auto;
}

/*ΣΤΥΛ ΓΙΑ ΤΟΥΣ ΠΙΝΑΚΕΣ ΠΟΥ ΘΑ ΕΜΦΑΝΙΖΟΝΤΑΙ ΟΙ ΚΑΜΠΑΝΙΕΣ */
table {
  width: 70%;
  margin-left: 10px;
  border-collapse: separate;
  font-family: Arial, sans-serif;
  background-color: #e6f4ff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

/* ΣΤΥΛ ΓΙΑ ΤΑ ΣΤΟΙΧΕΙΑ ΤΟΥ ΠΙΝΑΚΑ */
table th,
table td {
  border-radius: 4px;
  border: 1px solid #e6f4ff;
  padding: 12px;
  background-color: #e6f4ff;
  color: #4A90E2;
}

/*ΣΤΥΛ ΓΙΑ ΤΗΝ ΚΕΦΑΛΙΔΑ ΤΟΥ ΠΙΝΑΚΑ */
table th {
  background-color: #e6f4ff;
  color: #4A90E2;
}

h1 {
  text-align: left;
  color: #4A90E2;
}

h2 {
  text-align: left;
  color: #4A90E2;
}

/* ΣΤΥΛ ΓΙΑ ΤΟ ΚΕΙΜΕΝΟ*/
p {
  font-size: 1.2rem;
  margin: 10px 0;
  color: #4A90E2;
}
 /*ΣΤΥΛ ΓΙΑ ΤΑ BOLD ΓΡΑΜΜΑΤΑ */
strong {
  color: #4A90E2;
}

/*ΣΤΥΛ ΓΙΑ ΠΛΑΙΣΙΟ ΚΕΙΜΕΝΟΥ ΤΗΣ ΔΙΕΘΥΝΣΗΣ ΤΟΥ ΙΔΙΟΚΤΗΤΗ*/
span, .owner-box {
  font-weight: bold;
  color: #4A90E2;
}

/*ΣΤΥΛ ΓΙΑ ΤΟ ΠΛΑΙΣΙΟ ΚΕΙΜΕΝΟΥ ΠΟΥ ΕΜΦΑΝΙΖΕΤΑΙ Η ΣΥΝΔΕΔΕΜΕΝΗ ΔΙΕΥΘΥΝΣΗ */
.adress-box {
  display: inline-block;
  padding: 5px 15px;
  border: 1px solid #4A90E2;
  border-radius: 4px;
  background-color: #e6f4ff;
}

/*ΣΤΥΛ ΓΙΑ ΤΑ ΠΛΑΙΣΙΑ ΠΟΥ ΕΜΦΑΝΙΖΟΝΤΑΙ ΤΑ ΠΟΣΑ ΤΟΥ ΣΥΜΒΟΛΑΙΟΥ*/
.eth-box {
  display: inline-block;
  padding: 5px 15px;
  border: 1px solid #4A90E2;
  border-radius: 4px;
  background-color: #e6f4ff;
}

body {
  background-color: white;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  height: 100vh;
  overflow: visible;
}

.info-container div {
  display: inline-block;
  margin-left: 10px;
  padding: 5px;
  border: 1px solid #d1d1d1;
  border-radius: 5px;
  background-color: #f4f4f4;
}

.campaign-container {
  margin-left: 10px;
}

.campaign-container h2 {
  color: #4A90E2;
}

.campaign-container input {
  display: block;
  margin: 10px 0;
  padding: 8px;
  width: 20%;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.campaign-container button {
  background-color: #218838;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.campaign-container button:hover {
  background-color: #45a049;
}

.disabled-button {
  background-color: #ccc;
  cursor: not-allowed;
}

.warning {
  color: red;
  margin-top: 5px;
}

::placeholder {
  color: #4A90E2;
  opacity: 1;
}

.pledge-button {
  background-color: #218838;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
}

.cancel-button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
}

.fulfill-button {
  background-color: #FF9800;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
}

.pledge-button:hover, 
.cancel-button:hover,
.fulfill-button:hover {
  opacity: 0.9;
}

.claim-refund-btn {
  background-color: #218838;
  color: white;
  font-size: 18px;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  text-align: center;
  display: block;
  margin: 10px auto;
  transition: background-color 0.3s ease;
}

.claim-refund-btn:hover {
  background-color: #218838;
}

.claim-refund-btn:disabled {
  background-color: #c3c3c3;
  cursor: not-allowed;
}

.btn {
  font-size: 16px;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin: 5px 0;
  text-align: center;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.btn-green {
  background-color: #28a745;
  color: white;
}

.btn-green:hover {
  background-color: #218838;
  transform: scale(1.05);
}

.btn-red {
  background-color: #dc3545;
  color: white;
}

.btn-red:hover {
  background-color: #c82333;
  transform: scale(1.05);
}

input {
  margin: 10px 0;
  padding: 8px;
  width: 20%;
  border: 1px solid #ccc;
  border-radius: 5px;
}
